import React, { Component } from "react";
import Cube from "./lib/Cube";
import LS from "./lib/LS";

import Messenger from './core/Messenger';

class Login extends Component{
    constructor(props){
        super(props);
        this.state = {
            form:{
                username:"",
                password:""
            }
        };
    }

    render(){
        let {form} = this.state;
        return  (
            <div className="login-screen">
                <div className="login-form">
                    <div className="container">
                        <div className="form-inputs">
                            <h4>Đăng nhập</h4>
                            <div className="form-group">
                                
                                <div className="form-control-input input-group">
                                    <div className="input-group-prepend">
                                        <label htmlFor="login-username" className="input-group-text"><i className="fa fa-user"></i></label>
                                    </div>
                                    <input type="text" name="username" id="login-username" className="form-control" onChange={this.inputDidUpdate} onKeyUp={this.inputDidUpdate} placeholder="Nhập tài khoản" value={form.username} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-control-input input-group">
                                    <div className="input-group-prepend">
                                        <label className="input-group-text"><i className="fa fa-key"></i></label>
                                    </div>
                                    <input type="password" name="password" id="login-password" onChange={this.inputDidUpdate} onKeyUp={this.inputDidUpdate} className="form-control"  placeholder="Nhập mật khẩu" value={form.password} />
                                </div>
                            </div>
                            <div className="form-group buttons text-center">
                                <button type="button" className="btn btn-warning text-white" onClick={this.submitLogin}><i className="fa fa-unlock"></i> Đăng nhập</button> <button type="button" className="btn btn-default" onClick={this.showRegister}><i className="fa fa-pencil-square-o"></i> Đăng ký</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
    inputDidUpdate = (e) => {
        let input = e.target;
        let name = input.name;
        let value = input.value;
        let {form} = this.state;
        form[name] = value;
        this.setState({form});
    }

    submitLogin = () => {
        let {form} = this.state;
        if(!form.username) return alert("Vui lòng nhập tên tài khoản");
        else if(!form.password) return alert("Vui lòng nhập mật khẩu");
        Messenger.request('get-token', 'POST', form, (data)=>{
            if(!data.status){
                if(data.errors) alert(data.errors.join(", \n"));
            }else{
                this.emit('submit-login', data);
            }
        });
    }
    
    
    
    componentDidMount(){
        this.ioEventListener();
        if(Messenger.checkLogin()){
            this.props.history.push('/home');
        }else if(Messenger.access_token){
            this.emit('verify-account', {
                data:{
                    access_token:Messenger.access_token,
                },
                success:'login-success',
                fail:'login-fail',
            });
        }
    }

    emit = (event, data) => {
        Messenger.socket().emit(event, data);
    }

    
    ioEventListener = () => {
        const socket = Messenger.socket();
        // dang nhap thanh cong
        socket.on('login-success', (user) => {
            let {access_token} = user;
            LS.set('access_token', access_token);
            Messenger.set({user, access_token:access_token});
            this.showChatBox();
        });
        // da dang nhap
        socket.on('auth-error', (data)=>{
            //alert('Lỗi không xác định! \nVui lòng đăng nhập lại!');
        });

        // da dang nhap
        socket.on('login-fail', (data)=>{
            alert('Đăng nhập không thành công! \nVui lòng đăng nhập lại!');
            console.log(data);
            
        });
    }
    showRegister = () => {
        this.props.history.push('/register');
        return false;
    }

    showChatBox = () => {
        this.props.history.push('/messages');
        return false;
    }

}

export default Login;


